import { getImagesByQuery } from "./js/pixabay-api.js";
import { createGallery, clearGallery, showLoader, hideLoader } from "./js/render-functions.js";
import iziToast from "izitoast";
import "izitoast/dist/css/iziToast.min.css";

const form = document.querySelector(".form");
const searchInput = form.querySelector("input[name='search-text']");
const gallery = document.querySelector(".gallery");
const loadMoreBtn = document.getElementById("load-more-btn");
const loader = document.getElementById("loader");

let currentQuery = "";
let currentPage = 1;
const perPage = 15;
let totalHits = 0;

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–∞—Ä—Ç—É –Ω–æ–≤–æ–≥–æ –ø–æ—à—É–∫—É
form.addEventListener("submit", async (event) => {
  event.preventDefault();
  const query = searchInput.value.trim();

  if (!query) {
    iziToast.warning({
      title: "Warning",
      message: "Please enter a search query!",
    });
    return;
  }

  // –û—á–∏—â–∞—î–º–æ –≥–∞–ª–µ—Ä–µ—é —Ç–∞ —Å–∫–∏–¥–∞—î–º–æ —Å—Ç–æ—Ä—ñ–Ω–∫—É –Ω–∞ 1
  clearGallery();
  hideLoadMore();
  showLoader();

  currentQuery = query;
  currentPage = 1; // Reset page to 1 for new search

  // –û—Ç—Ä–∏–º—É—î–º–æ –ø–µ—Ä—à—ñ 15 –∑–æ–±—Ä–∞–∂–µ–Ω—å
  const data = await getImagesByQuery(query, currentPage, perPage);
  hideLoader();

  if (data.hits.length === 0) {
    iziToast.error({
      title: "Error",
      message: "Sorry, there are no images matching your search query. Please try again!",
    });
    return;
  }

  // –î–æ–¥–∞—î–º–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –≤ –≥–∞–ª–µ—Ä–µ—é
  totalHits = data.totalHits;
  createGallery(data.hits);

  // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ, —á–∏ —î —â–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  if (currentPage * perPage < totalHits) {
    showLoadMore(); // –ü–æ–∫–∞–∑—É—î–º–æ –∫–Ω–æ–ø–∫—É Load More, —è–∫—â–æ —î —â–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  } else {
    hideLoadMore();
    iziToast.info({
      title: "Info",
      message: "We're sorry, but you've reached the end of search results.",
    });
  }

  // –ü–ª–∞–≤–Ω–µ –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è –¥–æ –Ω–æ–≤–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
  scrollToNewImages();
});

// –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –Ω–æ–≤–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ Load More
loadMoreBtn.addEventListener("click", async () => {
  currentPage += 1;
  showLoaderUnderButton();

  // –û—Ç—Ä–∏–º—É—î–º–æ –Ω–æ–≤—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–ª—è –Ω–∞—Å—Ç—É–ø–Ω–æ—ó —Å—Ç–æ—Ä—ñ–Ω–∫–∏
  const data = await getImagesByQuery(currentQuery, currentPage, perPage);
  hideLoaderUnderButton();

  // –î–æ–¥–∞—î–º–æ –Ω–æ–≤—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–æ —ñ—Å–Ω—É—é—á–∏—Ö
  createGallery(data.hits);

  // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ, —á–∏ —î —â–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  if (currentPage * perPage >= totalHits) {
    hideLoadMore();
    iziToast.info({
      title: "Info",
      message: "We're sorry, but you've reached the end of search results.",
    });
  }

  // –ü–ª–∞–≤–Ω–µ –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è –ø—ñ—Å–ª—è –¥–æ–¥–∞–≤–∞–Ω–Ω—è –Ω–æ–≤–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
  scrollToNewImages();
});

// –ü–ª–∞–≤–Ω–µ –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è
function scrollToNewImages() {
  const lastImageCard = gallery.querySelector(".gallery-item:last-child");  // –æ—Å—Ç–∞–Ω–Ω—è –∫–∞—Ä—Ç–∫–∞ –≤ –≥–∞–ª–µ—Ä–µ—ó
  if (lastImageCard) {
    const cardHeight = lastImageCard.getBoundingClientRect().height;
    window.scrollBy({
      top: cardHeight * 2, // –ü—Ä–æ–∫—Ä—É—á—É—î–º–æ –≤–Ω–∏–∑ –Ω–∞ 2 –≤–∏—Å–æ—Ç–∏ –∫–∞—Ä—Ç–∫–∏
      behavior: "smooth", // –ü–ª–∞–≤–Ω–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∞
    });
  }
}

// –ü–æ–∫–∞–∑–∞—Ç–∏/—Å—Ö–æ–≤–∞—Ç–∏ –∫–Ω–æ–ø–∫—É Load More
function showLoadMore() {
  loadMoreBtn.hidden = false;
}

function hideLoadMore() {
  loadMoreBtn.hidden = true;
}

// –ü–æ–∫–∞–∑–∞—Ç–∏/—Å—Ö–æ–≤–∞—Ç–∏ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –ø—ñ–¥ –∫–Ω–æ–ø–∫–æ—é
function showLoaderUnderButton() {
  loader.classList.add("visible");
}

function hideLoaderUnderButton() {
  loader.classList.remove("visible");
}
